@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&family=Staatliches&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Nothing+You+Could+Do&family=Rock+Salt&family=Tangerine:wght@700&display=swap');

* {box-sizing: border-box;}

body {
  margin: 0;
  padding: 0;
  background: linear-gradient(to bottom, white,white,lightgreen, green);
  height:100vh;
}

.backBtn, .homeBtn {
  position: absolute;
  top: 10px;
  padding: 10px 20px;
  font-size: 1.7rem;
  cursor: pointer;
  z-index: 10;
  transition: .2s;
  color: rgb(104, 13, 104);
}

.backBtn {
  left: 15px;
}

.homeBtn {
  right: 15px;
}

.backBtn:hover, .homeBtn:hover {
  opacity: .7;
}

.header h1 {
  margin: 0;
  text-align: center;
  text-transform: uppercase;
  font-family: 'Staatliches', cursive;
  letter-spacing: 7px;
  font-size: 3rem;
  color:white;
  background: url('./matrix.gif');
  opacity: .2;
  transition: 1s;
}

.header h1:hover {
  opacity: 1;
}

.nav {
  background-color: darkgrey;
}

.lilink:hover, .fblink:hover {
  background-color: rgb(119, 139, 146);
  color:white;
}

h2 {
  text-align: center;
  margin: 0;
  /* font-family: 'Tangerine', cursive; */
  /* font-family: 'Nothing You Could Do', cursive; */
  font-family: 'Rock Salt', cursive;
}

.nav {
  display: flex;
  justify-content: flex-end;
  padding: 10px 50px;
  background: url('../src/codeimg.jpeg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.fbicon, .liicon, .ghicon {
  position: relative;
  margin: 0 10px;
  width: 40px;
  height: 40px;
  overflow: hidden;
  border-radius: 50%;
  opacity: 1;
  transition: .3s;
  cursor: pointer;
  filter:saturate(3);
}

.fbicon img {
  position: absolute;
  top: -32px;
  left: -34px;
  width: 300px;
  height: 300px;
}

.liicon img {
  position: absolute;
  right: -31px;
  top: -32px;
  width: 300px;
  height: 300px;

}

.fbicon:hover, .liicon:hover, .ghicon:hover  {
  opacity: .8;
  transform: scale(.9);
}

.ghicon {
  position: relative;
  margin: 0 20px;
  overflow: hidden;
  opacity: 1;
  transition: .3s;
  cursor: pointer;
  filter:saturate(3);
}

.ghicon img {
  width: 40px;
  border-radius: 50%;
}

ul {
  margin: 10px 40px;
}